.card-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0.5rem 0.5rem 0.8rem rgba(0, 0, 0, 0.4);
}
.card-header img {
  height: 15rem;
  width: 100%;
}

.card-body {
  text-align: center;
}
.card-body-paragraph {
  font-size: 1.2rem;
  margin-bottom: 0.6rem;
}
.card-body-list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 1.1rem;
}

.card-body-item {
  text-align: left;
  margin-bottom: 0.6rem;
}

.card-footer-link {
  display: block;
  padding: 0.5rem 0;
  font-size: 1.3rem;
  background-image: linear-gradient(
    to right,
    rgb(155, 46, 13),
    rgb(255, 123, 0)
  );
  border: 1px solid rgba(0, 0, 0, 0.4);
  color: rgb(255, 255, 255);
}
.card-footer-link {
  background-color: white;
}
.card-footer-link:hover .fab,
.card-footer-link:hover .fas {
  color: rgb(255, 174, 0);
}

.fab,
.fas {
  font-size: 1.5rem;
  color: rgb(255, 255, 255, 0.4);
}
