.nav {
  box-shadow: 0.5rem 0.6rem 1rem rgba(0, 0, 0, 0.8);
  background-image: linear-gradient(
    to bottom,
    rgb(155, 46, 13),
    rgba(155, 46, 13, 0.5)
  );
}

.navigation__nav {
  display: flex;
  list-style: none;
}

.navbar__branding {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
}

.nav__container {
  width: 80%;
  margin: 0 auto;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation__item:not(:last-child) {
  margin-right: 3rem;
}

.navigation__link {
  text-decoration: none;
  color: white;
  text-transform: uppercase;
}
