.about {
  position: relative;
  height: 28rem;
  width: 90%;
  margin: 3rem auto;
}
.about-image {
  position: absolute;
  height: 28rem;
  width: 80%;
  z-index: 2;
}

.about-header {
  font-size: 3rem;
  background-image: linear-gradient(
    to right,
    rgb(233, 58, 4) 0%,
    rgb(240, 98, 98) 10%,
    transparent 100%
  );
  background-clip: text;
  color: transparent;
}

.about-text {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: rgba(255, 255, 255, 0.8);
  transform: translateY(20%);
  z-index: 22;
  background-color: rgba(247, 220, 220, 0.7);
  box-shadow: 0.4rem 0.4rem 0.7rem rgba(0, 0, 0, 0.5);
}
.about-heading {
  font-size: 1.5rem;
  text-align: left;
  margin-left: 0.6rem;
}
.about-paragraph {
  font-size: 1.16rem;
  margin-left: 0.6rem;
  font-weight: 400;
}
